import Image from "next/image";
import headerSlider1 from "../../public/imgs/header1.jpg";
import headerSlider2 from "../../public/imgs/header2.jpg";

export default function Home() {
  const [activeImage, setActiveImage] = useState(0);

  useEffect(() => {
    const intervalId = setInterval(() => {
      setActiveImage((prevActiveImage) => (prevActiveImage + 1) % 2);
    }, 3000); // change image every 3 seconds

    return () => clearInterval(intervalId);
  }, []);

  return (
    <>
      <header className="home min-h-screen bg-cover flex overflow-scroll">
        <div className="slider">
          <Image
            alt="first Image slider "
            className={`w-screen bg-cover ${activeImage === 0 ? "active" : ""}`}
            src={headerSlider1}
          ></Image>
          <Image
            alt="first Image slider"
            className={`w-screen bg-cover ${activeImage === 1 ? "active" : ""}`}
            src={headerSlider2}
          ></Image>
        </div>
      </header>
    </>
  );
}